import React, { useEffect } from "react";
import logo from "./logo.svg";
import "./App.css";

function App() {
	useEffect(() => {
		console.log("going to fetch items");
		fetchItems();
	}, []);

	const fetchItems = async () => {
		console.log("fetching items");

		// if in development then make the request against the dev server
		const url =
			process.env.NODE_ENV === "development"
				? `${process.env.DEB_API_SERVER}/api/pages?websitePath=/notes/linux.*&regex=true`
				: `/api/pages?websitePath=/notes/linux.*&regex=true`;

		// const url = `/api/pages?websitePath=/notes/linux.*&regex=true`;
		console.log(url);
		const data = await fetch(url, { method: "get" });
		console.log(await data.json());
	};

	return (
		<div className="App">
			<header className="App-header">
				<img src={logo} className="App-logo" alt="logo" />
				<p>
					Edit <code>src/App.js</code> and save to reload.
				</p>
				<a
					className="App-link"
					href="https://reactjs.org"
					target="_blank"
					rel="noopener noreferrer"
				>
					Learn React
				</a>
			</header>
		</div>
	);
}

export default App;
